#fp-nav ul li a span, .fp-slidesNav ul li a span{ 
  background: #fff !important; 
} 

h1{
  font-family: 'ALBA', Arial, Helvetica, sans-serif;
  color: white;
  font-size: 4rem;
  position: relative;
}






.particles-js canvas {
    display: block;
    vertical-align: bottom;
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
    transition: opacity .8s ease, -webkit-transform 1.4s ease;
    transition: opacity .8s ease, transform 1.4s ease;
    transition: opacity .8s ease, transform 1.4s ease, -webkit-transform 1.4s ease
  }
  
.particles-js {
    width: 100%;
    height:100vh;
    position: fixed;
    z-index: -10;
    top: 0;
    left: 0;
    background: linear-gradient(to right, rgb(32, 32, 83), rgb(88, 66, 109));

  }

.home{
    color: white;
  }

.info{
    position: relative;
    color: white;
    font-weight: bold;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
    font-size: 1em;
}

.about::before{
    background: url(/static/media/aboutBackground.4c9f2260.PNG);
    background-size: cover !important;
    background-position: center !important;
    background-repeat: no-repeat !important;
    
    content: "";
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
    filter: blur(3px);
    -webkit-filter: blur(3px);
  }



  .myPic{
    height: 300px;
    width: auto;
    border-radius: 50%;
  }


.portfolio{
    text-align: center;
    display: inline-block;
    margin: 0;
    color: black;
}



.contact{
    background-color:rgb(135, 146, 163, 0.4);
    color: black;
    margin: 0 auto;
    padding: 2rem 2rem;
    width: 50rem;
    
}
.icons{
  margin: 0 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

@media screen and (max-width:800px){
  .contact{
      width: 20rem;
  }
}


.footer{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
}
i{
    font-size: 3rem;
    margin: 0 1rem;
}

@media screen and (max-width:800px){
    i{
        margin:0;
    }
  }
a{
    color: white !important;
    text-decoration: none !important;
}


